nav {
    width: 100vw;
    height: 5rem;
    display: grid;
    place-items: center;
    background: var(--color-black);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    box-shadow: 0 1rem 1.5rem hsla(var(--color-black),
    68% 42% 20%);
    transition: var(--trainsition);
}

.nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

}


.nav__logo {
    width: 3.5rem;
    border-radius: 50%;
    overflow: hidden;
}

.nav__menu {
    display: flex;
    gap: 3.5rem;
}

#theme__icon {
    background: transparent;
    color: var(--color-black);
    font-size: 2rem;
    cursor: pointer;
}

/* HIDE THE NAV MENUS ON TABLETS AND PHONES*/
@media screen and (max-width: 1024px) {
    .nav__menu {
        display: none;
    }
    
}